import React, { Component } from 'react';
import {
  BrowserRouter as Router,
  Route,
  Link
} from 'react-router-dom'
import FeedItem from './feed_item'

class Feed extends Component {
  state = {
    items: []
  }

  componentDidMount() {
    fetch('https://wikibits.herokuapp.com/tweets')
      .then((data) => data.json())
      .then((items) => {
        this.setState({items})
      })
  }

  render() {
    let { items } = this.state
    
    return (
      <div style={{margin: '2em 10em 0em 10em'}}>
        {items.map((item) => (
            <FeedItem
              key={item.id}
              data={item}
            />
          )
        )}
      </div>
    )
  }

  }

export default Feed


